<template>
	<div class="float-content">
		<b-alert v-model="error" class="p-1" variant="danger">
			مشکلی در اتصال به اینترنت یا سرور پیش آمده است
		</b-alert>
		<div class="text-center mb-2">
			<div class="d-flex align-items-center justify-content-center mb-1">
				<b-avatar class="mr-1" size="40" variant="light-success">
					<book-icon :style="{ color: 'var(--success)' }" size="30" />
				</b-avatar>
				<h4 class="m-0">لیست هزینه‌ها</h4>
			</div>
			<span>
				در این بخش لیست هزینه‌هایی  که از قبل ثبت کرده‌اید مشاهده می‌کنید. برای ثبت هزینه جدید دکمه بنفش را فشار دهید.
			</span>
		</div>

		<div class="align-items-center my-2">
			<label class="filter-label" style="margin: 0 0.4rem; ">
				نمایش براساس:
			</label>
			<custom-vue-multi-select
				v-model="filterCostBy"
				placeholder="نمایش همه"
				:options="costsFilterOptions"
				based-on="expense_category_name"
				target-key="expense_category_id"
			/>
		</div>

		<div class="mb-3">
			<div
				v-if="loading"
				class="d-flex flex-column justify-content-center align-items-center"
				style="margin-top: 150px"
			>
				<img :src="require('@/assets/images/logo/logo.svg')" alt="Logo" width="70" />
				<b-spinner
					style="margin-top: 10px; width: 3rem; height: 3rem"
					variant="primary"
				/>
			</div>
			<template>
				<b-card
					v-for="(cost, index) in filteredDeptorList"
					:key="index"
					class="w-100 mt-1"
				>
				<div>
            <a
              v-b-toggle="`accordion-${index}`"
              class="px-0 w-100"
            >
					<div class="d-flex justify-content-between">
						<div class="d-flex align-items-center">
							<b-avatar class="bill-item-avatar" size="30">
								<img
									:src="require('@/assets/icons/money-cyen.svg')"
									alt="bill-debt"
									draggable="false"
									width="25"
								/>
							</b-avatar>
							<h5 class="mb-0">{{ cost.expense_name }}</h5>
						</div>
						<div>
							<span class="font-weight-bolder ml:0.3rem text-size:1.2rem">
								{{ cost.locale_expense_fee }}
							</span>
						</div>
					</div>
					<div class="d-flex justify-content-between mt:0.5rem align-items-center">
						<div class="ml-3">
							<span class="how-to-divide-text">نحوه پرداخت: {{ cost.receipt_type }}</span>
						</div>
						<div>
							<span class="date-text">{{ cost.locale_expense_date }}</span>
							<img
								:src="require('@/assets/icons/calendar-icon.svg')"
								alt="bill-debt"
								draggable="false"
								width="18"
							/>
						</div>
					</div>
				</a>
          </div>
					<b-collapse
            :id="`accordion-${index}`"
            :v-model="show"
            accordion="my-accordion"
            role="tabpanel"
          >
		  <div class="card-border my-2" />
			<div class="d-flex justify-content-between align-items-center pb-1">
				<div class="ml-3">
					سرفصل:
					<span>{{ cost.expense_category_name }}</span>
				</div>
              <div

                class="d-flex justify-content-end"
>

                <div class="text-muted">

                  <feather-icon
                    style="cursor: pointer;"
                    size="20"
                    icon="EditIcon"
                    class="ml-2"
					 @click="edit(cost)"
/>
                </div>
                <div class="text-muted">
                  <feather-icon
                    size="20"
                    style="cursor: pointer;"
                    icon="Trash2Icon"
                    class="ml-2"
					  @click="showDeleteModalfunc(cost)"
/>
                </div>
              </div>
			</div>
          </b-collapse>
				</b-card>
				 <Modal
          :pending="deletePending"
          :show-modal="showDeleteModal"
		  title="هزینه ثبت شده حذف خواهد شد
			آیا اطمینان دارید ؟"
          @toggleModal="showDeleteModal = !showDeleteModal"
          @onHide="showDeleteModal = false"
          @confirmModal="ContinueDeleting(selected)"
        />
			</template>
		</div>
		<router-link class="mx-1 mt-auto" :to="{ name: 'costRegistry' }">
			<div
				class="px-2"
				style="
					right: 50%;
					transform: translate(50%, -50%);
					position: fixed;
					width: 100%;
					max-width: 500px;
					background: #f8f8f8;
					bottom: 20px;
					padding: 10px 26px 26px 26px;
				"
			>
				<b-button block variant="primary">
					<img
						:src="require('@/assets/icons/add_icon.svg')"
						alt="add"
						draggable="false"
						width="24"
						class="add-icon"
					/>
					ثبت هزینه جدید
				</b-button>
			</div>
		</router-link>
	</div>
</template>

<script>
import CustomVueMultiSelect from "@/@core/components/custom-vue-multi-select/CustomVueMultiSelect.vue";
import {
 BButton, BAvatar, BCard, BAlert, BSpinner, BCollapse, BOverlay
} from "bootstrap-vue";
import { v4 as uuidv4 } from "uuid";
import BookIcon from "@/@core/components/svg-icons/BookIcon.vue";
import managerService from "@/api/managerService";
import Modal from '@core/components/Modal.vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	components: {
		BButton,
		BAvatar,
		BCard,
		BAlert,
		CustomVueMultiSelect,
		BookIcon,
		BSpinner,
		BCollapse,
        BOverlay,
		Modal
	},
	data() {
		return {
			searchInput: "",
			costsFilterOptions: [],
			filterCostBy: [],
			costs: [],
			error: false,
			loading: false,
			deletePending: false,
			showDeleteModal: false,
			selected: {},
			showLoading: true,
			show: false,
		};
	},
	computed: {
		filteredDeptorList() {
			if (this.filterCostBy.length > 0) {
				return this.costs.filter((item) => this.filterCostBy.some((filter) =>
					filter.targetValue === item.expense_category_id
				));
			}
			return this.costs;
		},
	},
	watch: {},
	async created() {
		await this.loadData()
	},
	methods: {
		async loadData() {
				try {
			this.loading = true;
			const { data: expenseFilter } = await managerService.expenseName()
			const costsFilterList = [];
			for (const id in expenseFilter.list) {
				costsFilterList.push({
					expense_category_id: +id,
					expense_category_name: expenseFilter.list[id],
				});
			}
			this.costsFilterOptions = costsFilterList;

			const { data } = await managerService.expense()
			this.costs = data.expense_list;

			this.loading = false;
		} catch (err) {
			console.log(err);
		}
	},
	showDeleteModalfunc(selected) {
      this.showDeleteModal = true
      this.selected = selected
    },
	async ContinueDeleting(selected) {
      this.deletePending = true
      const data = { expense_id: selected.expense_id }
      await managerService.costsDelete(data).then(res => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: res.data.message,
            icon: 'EditIcon',
            variant: 'success',
          }
        })
        this.deletePending = false
        this.showDeleteModal = false
        this.costs = this.costs.filter(item => item.expense_id != selected.expense_id)
		this.loadData()
      })
	},
 edit(selected) {
      this.$router.push({
 path: '/cost/update',
query: {
 id: selected.expense_id,
 expense_name: selected.expense_name,
receipt_type: selected.receipt_type,
receipt_type_id: selected.receipt_type_id,
receipt_number: selected.receipt_number,
expense_fee: selected.expense_fee,
expense_category_id: selected.expense_category_id,
expense_category_name: selected.expense_category_name,
locale_expense_date: selected.locale_expense_date,
}
})
    },
		getUniqueString() {
			return uuidv4();
		},
	},
};
</script>

